













import { mapState, mapMutations } from 'vuex';
import { Component, Vue } from 'vue-property-decorator';

@Component({
  computed: {
    ...mapState([
      'categories',
    ]),
  },
  methods: {
    ...mapMutations(['categorize']),
  },
})

export default class Random extends Vue{
  
  categories!: string[];
  categorize!: () => void;
  
  created() {
    document.title = 'Categories';
  }

  mounted() {
    this.categorize();
  }
}
